@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);

*:focus{
    outline: none;
    box-shadow: none;
}

*{
    padding: 0;
    margin: 0;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    box-sizing: border-box;
}

:root{
    --size-divisor: 1;
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body{
    font-family: 'Roboto';
}

::-webkit-scrollbar {
    display: none;
}
  


.content{
    display: flex;
    flex-direction: column;
}

.section-content{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 100vh;
    margin-left: 10rem;
    background: #D8DBE2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 5rem 0;
}

.watermark {
    position: fixed;
    bottom: .5rem;
    right: .5rem;
    color: #1B1B1E;
    font-weight: 400;
    font-size: 1.7rem
}
@media (max-width: 1920px) {
    :root {
      --size-divisor: 1;
    }
  }

  @media (max-width: 1440px) {
    :root {
      --size-divisor: 1.2;
    }
  }

  @media (max-width: 768px) {
    :root {
      --size-divisor: 1.4;
    }
  }
.nav-ul{
    list-style: none;
    margin-top: 25vh;
}

.nav-link{
    margin-bottom: 3.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.nav-link:first-child{
    margin-top: 3.1rem
}

.nav-img {
    width: 6.9rem;
    height: 4.9rem;
    fill: #D8DBE2;
}

.nav-img:hover{
    fill: #F3F4F6;
}

.nav-link:hover > .nav-img{
    fill: #F3F4F6;
    cursor: pointer;
}

.nav-label:hover{
    cursor: pointer;    
}

.nav-label{
    background: #1B1B1E;
    color: #F3F4F6;
    font-size: 1.8rem;
    padding: .25rem .9rem;
    border-radius: .35rem;
}

.nav-logo{
    position: absolute;
    width: 7.9rem;
    height: 7.9rem;
    border-radius: 50%;
    border: .2rem solid #D8DBE2;
    left: 50%;
    top: 1.6rem;
    transform: translate(-50%, -10%)
}

.nav-logo + h1{
    display: none;
}

.nav-bar{
    display: flex;
    position: fixed;
    justify-content: center;
    background: #1B1B1E;
    width: 10rem;
    height: 100vh;
    top: 0;
    left: 0;
}

.line{
    background: #D8DBE2;
    width: 4rem;
    height : .6rem;
    margin: .8rem 0;
    border-radius: 1px
}

.hamburger-button{
    display: none;
    position: absolute;
    top: 50%;
    right: 1.6rem;
    transform: translate(-10%, -50%)
}
.nav-img + span{
    opacity: 0;
    transition: none .1s ease-out;
    transition-property: opacity, left, transform;
    transition-delay: .1s;
    position: fixed;
    left: 2rem;
}

.nav-link:hover > span {
    opacity: 10;
    left: 10.2rem;
    transform: translateY(-55%)
}




@media only screen and (max-width: 1280px){

}

@media only screen and (max-width: 720px){
    .nav-bar{
        width: 100vw;
        height: 7.5rem;
    }
    .nav-link{
        display: none;
    }
    .section-content{
        margin: 0
    }
    .nav-logo{
        display: none;
    }
    .hamburger-button{
        display: block;
    }
}


.menu{
    display: flex;
    flex-direction: row;
}
.menu-holder{
    flex: 1 0;
    margin-left: 10rem;
    width: 100px;
    height: 100vh;
    background: #A9BCD0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.name-div{
    display: flex;
    flex-direction: column;
}

.name-heading{
    font-size: 6.4rem;
    color: #1B1B1E;
}

.icon-holder{
    display: flex;
    justify-content: center;
}

.socials-logo-anchor{
    margin: .1rem 1.8rem;
}

.socials-logo{    
    width: 4.9rem;
    height: 4.9rem;
    fill: #1B1B1E;
}

.socials-logo:hover{
    fill: black;
}

.arrow-down{
    position: absolute;
    bottom: 1rem;
}

.arrow-down-icon{
    fill:  #1B1B1E;
    width: 2.4rem;
}

.arrow-down-icon:hover{
    fill: black;
    cursor: pointer;
}
@media only screen and (max-width: 1280px){

}

@media only screen and (max-width: 720px){
    .menu-holder{
        margin: 0
    }
}

.card{
    background: #F3F4F6;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    box-shadow: 0 .1rem .4rem 0 #1B1B1E;
    box-sizing: border-box;
}
.about-me-holder{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-me-heading{
    font-size: calc(4.8rem / var(--size-divisor));
}

.card-about-me{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 2rem;
    flex-shrink: 1;
    width: calc(76.8rem / var(--size-divisor));
    height: calc(51.2rem / var(--size-divisor));
}

.about-me-paragraph{
    font-size: calc(3rem / var(--size-divisor));
    font-weight: 300;
    margin-left: calc(2rem / var(--size-divisor));
}

.about-me-button-holder{
    display: flex;
    align-items: space-evenly;
}

.about-me-button{
    width: calc(25.6rem / var(--size-divisor));
    height: calc(8rem / var(--size-divisor));
    margin-bottom: calc(5rem / var(--size-divisor));
    margin:  0 calc(2rem / var(--size-divisor));
    border-radius: 5rem;
    border: 0;
    background: #1B1B1E;
    font-size: calc(2.8rem / var(--size-divisor));
    color: #D8DBE2;
}

.about-me-button:hover{
    background: black;
    color: #F3F4F6;
    cursor: pointer;
}

.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.services-heading{
    font-size: 4.8rem;
}

.service-card-holder{
    display: flex;
    flex-wrap: wrap;
}

.card-service{  
    margin: 0 2rem; 
    width: 25.6rem;
    height: 57.6rem;
}

.service-text{
    margin: 1.5rem;
    font-size: 2.7rem;
    text-align: center;
    font-weight: 300;
}

.service-text b{
    font-weight: 400;
}

.services-footer{
    font-size: 2.4rem;
    font-weight: 300;
}

.services-footer a {
    text-decoration: none;
    color: #FF003D;
}

.services-footer a:hover {
    cursor: pointer;
}

.projects-holder{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.projects-heading{
    font-size: 4.8rem;
}

.projects-ul{
    list-style: none;
    display: flex;
    font-size: 1.8rem;
    margin-top: 5rem;
}

.projects-li{
    margin: 0 .5rem;
}

.projects-card-holder{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 86.4rem;
    width: 108.8rem;
    margin: 4rem 0;
}

.projects-footer{
    font-size: 2.4rem;
    font-weight: 300;
}

.projects-footer a {
    text-decoration: none;
    color: #FF003D;
}

.project{
    background: #F3F4F6;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    box-shadow: 0 .1rem .4rem 0 #1B1B1E;
    width: 25.6rem;
    height: 25.6rem;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 50%;
}

.project-holder{
    margin: 1.6rem 1.6rem;
    text-decoration: none;
    color: #1B1B1E;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.6rem;
    width: 25.6rem;
    overflow: break-word;
    word-break: break-all;
    text-align: center;
}

.project-name{
    margin-top: 1rem
}

.project-description{
    font-size: 2.6rem;
    font-weight: 200
}
.contact-me{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.contact-heading{
    font-size: 4.8rem;
}

.form-contact{
    display: flex;
    flex-direction: column;
    align-items: center
}

.fl-div{
    margin-bottom: 2.1rem;
}

.fl-input{
    margin: 0 1rem;
}

.form-input{
    width: 58.7rem;
    height: 7.6rem;
    font-size: 2.4rem;
    border: solid #1B1B1E .2rem;
    border-radius: 3rem;
    /* margin: 2.1rem 1rem; */
    padding: 2rem
}

.fsubject{
    width: 119.4rem;
    margin: 2.1rem 1rem;
}

.fmessage{
    margin: .8rem 1rem 2.1rem 1rem;
    width: 119.4rem;
    max-height: 40.6rem;
    min-height: 36.6rem;
    font-size: 2.4rem;
    font-family: roboto; 
    resize: vertical;
    box-sizing: border-box;
}

.fbutton-holder{
    align-self: center;
}

.submit-button{
    width: 25.6rem;
    height: 8rem;
    border-radius: 5rem;
    border: 0;
    background: #1B1B1E;
    font-size: 2.8rem;
    color: #D8DBE2;
}

.submit-button:hover{
    background: black;
    color: #F3F4F6;
    cursor: pointer;
}


